@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
  margin     : 0;
  padding    : 0;
  box-sizing : border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  background: rgb(198, 238, 238);
}
.todo-item {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  padding        : 0.5em 1em;
  margin-bottom  : 0.4em;
  transition     : 300ms;
}

.todo-item.done {
  background-color: rgba(192, 192, 192, 0.397);
  border-radius   : 4px;
}

.todo-item textarea {
  background   : none;
  outline      : none;
  border       : none;
  color        : black;
  font-size    : 1.2em;
  text-align   : center;
  width        : 80%;
  margin-left  : 0.2em;
  resize       : none;
  overflow     : hidden;
  transition   : 300ms;
}

.todo-item.done textarea {
  text-decoration: line-through;
}

.todo-item textarea:focus {
  border-bottom: 1px solid teal;
}

.todo-item input[type="checkbox"] {
  display: block;
  width  : 28px;
  height : 28px;
}

.todo-item input[type="checkbox"]:checked {
  background: rgb(41, 153, 153);
  border    : 1px solid teal;
}


.todo-item button {
  background: none;
  border    : none;
  padding   : 0;
  margin    : 0;
}

.todo-item button img {
  height: 32px;
  width : 32px;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.todo-item button:hover {
  -webkit-animation: rotate 500ms normal 0ms;
          animation: rotate 500ms normal 0ms;
}
.todo-add-form {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  padding        : 0 2em;
  margin-bottom  : 1em;
}

.new-todo-wrapper {
  border-bottom: 1px solid aquamarine;
  width        : 80%;
  transition   : 300ms;
}

.todo-add-form textarea {
  font-size : 1em;
  display   : block;
  width     : 100%;
  padding   : 1em 0;
  background: none;
  border    : none;
  outline   : none;
  resize    : none;
  overflow  : hidden;
}

.new-todo-wrapper:focus-within {
  border-bottom: 1px solid teal;
}

.todo-add-form textarea:focus::-webkit-input-placeholder {
  /* color: white; */
  font-size: 1.2em;
}

.todo-add-form textarea:focus:-ms-input-placeholder {
  /* color: white; */
  font-size: 1.2em;
}

.todo-add-form textarea:focus::placeholder {
  /* color: white; */
  font-size: 1.2em;
}

.todo-add-form button[type="button"] {
  display         : block;
  padding         : 0.5em;
  font-size       : 1em;
  color           : white;
  background-color: teal;
  border          : 1px solid teal;
  border-radius   : 4px;
  transition      : 200ms;
}

.todo-add-form button[type="button"]:hover {
  background-color: white;
  color           : teal;
}
.todo-list-title {
  padding-left : 2em;
  border-radius: 8px;
}

.todo-list-title:focus-within {
  box-shadow: inset 0px 0px 5px teal;
}

.todo-list-title textarea {
  background : none;
  outline    : none;
  border     : none;
  font-size  : 1.8em;
  font-weight: 550;
  width      : 80%;
  height     : auto;
  resize     : none;
  overflow   : hidden;
}
.todo-list-container {
  padding      : 1em;
  margin       : 1em;
  width        : 400px;
  border       : 1px solid olivedrab;
  border-radius: 8px;
  background   : snow;
}

button.delete-list {
  color        : firebrick;
  background   : none;
  border       : 1px solid firebrick;
  border-radius: 4px;
  padding      : 10px;
  font-size    : 1em;
  transition   : 300ms;
}

button.delete-list:hover {
  color     : white;
  background: firebrick;
}
.add-todo-list-container {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
}

button.add-todo-list {
  background   : none;
  border       : none;
  border-radius: 4px;
  padding      : 0;
  font-size    : 0;
}

button.add-todo-list:hover {
  box-shadow: 0px 0px 5px white;
}

button.add-todo-list img {
  height: 42px;
  width : 42px;
}
.nav-wrapper {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
}

.logo-container {
  background     : rgb(22, 187, 187);
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-right   : 2em;
  padding        : 0 0.5em;
}

.logo {
  height : 48px;
  width  : 48px;
  display: block;
}

.nav-text {
  color         : white;
  font-size     : 1.8em;
  text-transform: capitalize;
  margin-right  : 2em;
}

@media screen and (max-width: 400px) {
  .nav-wrapper {
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-bottom: 1em;
  }

  .nav-text {
    margin-right  : 0;
    padding-top   : 1em;
    padding-bottom: 0.5em;
  }

  .logo-container {
    width       : 100%;
    margin-right: 0;
  }
}
.no-tasks-message-container {
  height         : calc(100vh - 100px);
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
  color          : white;
  text-shadow    : 0px 0px 3px teal;
  padding        : 0 1em;
  text-align     : center;
}

@media screen and (max-width: 400px) {
  .no-tasks-message-container {
    height: calc(100vh - 200px);
  }
}

.no-tasks-message-container h1 {
  font-size: 3em;
}

.no-tasks-message-container h2 {
  font-size: 2em;
}
.todo-desk-container {
  display       : -webkit-flex;
  display       : flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.todo-desk {
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-wrap      : wrap;
          flex-wrap      : wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items    : flex-start;
          align-items    : flex-start;
}

